.blogs
 {
    font-family: 'Montserrat','Roboto';
    position: relative;
    min-height: 100vh;
    background-image:
        linear-gradient(to bottom, rgba(50, 65, 88, 0.82), rgba(50, 65, 88, 0.52)),
        url('./blog.jpg');
    overflow: visible;
    background-size: cover;
    background-position: 30% 30% ;
}
.blogWrapper{
    padding: 10vw 20vw;
}
.blog{
    background-color: rgba(50, 65, 88,0.5);
    padding-top:3%;
    height: 30vw;
    color:#DBB89A;
    text-align: center;
    border: 2px solid #DBB89A;
}
.blogImage{
    width:70%;
}