.education {
    font-family: 'Montserrat', 'Roboto';
    position: relative;
    background-image:
        linear-gradient(to bottom, rgba(50, 65, 88, 0.82), rgba(50, 65, 88, 0.52)),
        url("./education.jpg");
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;

}



.eEText {
    padding-top: 3%;
    text-align: center;
}




.rightEducation {
    border: 1px solid white;

}

.educationAndExperienceWrapper {

    padding: 15vw 20vw 5vw 20vw;
}

.educationTitle {
    color: #DBB89A;
    position: absolute;
    top: 15%;
    left: 50%;
    transform: translate(-50%, -50%);
}



.year {
    color: #DBB89A;
    font-weight: bolder;
}

.school {
    color: white;
}

.program {
    color: #DBB89A;
}



.experienceWrapper {
    border-left: 1px solid white;
}




